import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ServerMessage } from '../../services/message.modal';
import { ServerConnect } from '../../services/server-connect.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.page.html',
  styleUrls: ['./register.page.scss'],
})
export class RegisterPage implements OnInit {

  public form = {
    name: '',
    phone: '',
    email: '',
    password: ''
  }
  constructor(private dataService: ServerConnect,
    private router: Router) { }

  ngOnInit() {
    this.dataService.getData$('registerUser').subscribe((data) => {
      if(data.result === 'Success') {
        this.router.navigate(['/tabs']);
      }
    })
  }

  public onClick(data: any): void {
    console.log("clicked register", this.form);
    let msg: ServerMessage = new ServerMessage('set', 'registerUser', this.form);
    this.dataService.sendMessage(msg);
  }
} 
